}
},
mounted(){

},
methods: {
checkValue(value){
if (this.account && this.password) {
this.isDisabled = false;
} else {
this.isDisabled = true;
}
},

login(){
this.showLoading = true;
this.$http.get(apiHost + 'router_stat/auth/api/login', {
params:{
name: this.account,
password: this.password
}
}).then(res => {
this.showLoading = false;
if (res.data.result === 1) {
this.$router.push({name: 'home'});
} else if(res.data.result === 0) {
this.alertTitle = "请重新输入";
this.alertContent = "账号或密码错误";
this.showAlert = true;
}
}).catch(res => {
this.alertTitle = "网络错误";
this.alertContent = res;
this.showAlert = true;
})
}
}
}



